import { Form, Select, Input, DatePicker, Button, InputNumber  } from "antd";
import { useCallback, useState } from "react";
import { useNavigate, useOutletContext  } from "react-router-dom";
import { Products } from "./products";

const { TextArea } = Input;

export const Order = () => {
    const navigate  = useNavigate();
    const [product, setProduct] = useState();
    const [formData, setFormData] = useState({
        height: 0,
        width: 0,
        remark: ''
    });
    const [, setSelectData] = useOutletContext();

    const selectHandle = useCallback((val) => {
        const p = Products.find(p => p.value === val)
        setProduct(p)
    }, []);

    const inputHanel = (key) => (val) => {
        setFormData(pre => ({...pre, [key]: val}))
    }
    const onFinish = useCallback((values) => {
        console.log(values);
        const data = {...values, currentPro: product};
        setSelectData(data);
        Promise.resolve().then(() => {
            navigate('/print');
        })
    }, [product, navigate])

  return (
    <div className="order-page">
      <Form onFinish={onFinish}>
        <Form.Item rules={[{ required: true, message: '' }]} label="" name="useName">
          <Input />
        </Form.Item>
        <Form.Item label=""  rules={[{ required: true, message: '' }]}   name="time">
          <DatePicker showTime  />
        </Form.Item>
        <Form.Item label=""  rules={[{ required: true, message: '' }]}  name="prodNum">
          <Input />
        </Form.Item>
        <Form.Item label=""  rules={[{ required: true, message: '' }]}  name="currentPro">
          <Select onSelect={selectHandle}>
            {Products.map((pro) => (
              <Select.Option key={pro.name} value={pro.value}>{pro.name}</Select.Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item label="h"  rules={[{ required: true, message: '' }]}  name="height">
          <InputNumber  onChange={inputHanel('height')} addonAfter="mm" />
        </Form.Item>
        <Form.Item label="w" name="width"  rules={[{ required: true, message: '' }]} >
          <InputNumber   onChange={inputHanel('width')}  addonAfter="mm"/>
        </Form.Item>
        <Form.Item label="" name="remark">
          <TextArea rows={4}  />
        </Form.Item>
        <Form.Item>
          <Button className="sub-btn" type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>

      <div className="select_result">
        {product && <>
            {product.params.map(param => <div key={param.desc} className="param-item">
            <span className="sp1">{param.desc}</span> 
            {param.subParams.map(subP => <span key={subP.desc} className="p2">
            <i>{subP.desc}{subP.alias.h}{subP.h(formData.height).toFixed(2)}{subP.alias.w}{subP.w(formData.width).toFixed(2)}</i>
            </span>)}
            </div>)}
        </>} 
      </div>
    </div>
  );
};
